/*
  Define your resets here or use something like Normalize.css if you like
 */

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

*{margin:0;padding:0;background:none;border:none;font:inherit;color:inherit;vertical-align:baseline;box-sizing:border-box;border-radius:0}

html{height:100%;min-height:100%;-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto;word-wrap:break-word;word-wrap:break-word;-webkit-text-size-adjust:100%;-moz-text-size-adjust:100%;-ms-text-size-adjust:100%;text-size-adjust:100%;font-family:system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";font-size:200%;font-size:calc(0.5em + 1vw + 1vh + 0.5vmin);-webkit-tap-highlight-color:rgba(0,0,0,0);text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

body{display:-webkit-box;display:-ms-flexbox;display:flex;align-items:stretch;justify-content:center;height:100%;margin:0;padding:0;color:#ffffff;line-height:1.1;background:-webkit-gradient(linear, left top, right top, from(#f857a6), to(#ff5858));background:linear-gradient(to right, #f857a6, #ff5858)}

section{display:-webkit-box;display:-ms-flexbox;display:flex;flex-direction:column;align-items:center;justify-content:center;flex-grow:1;flex-basis:65%}

aside{display:-webkit-box;display:-ms-flexbox;display:flex;flex-direction:column;align-items:center;justify-content:center;flex-grow:1;background-color:#ffffff}

@media only screen and (max-width: 960px){body{flex-direction:column}section{flex-grow:0}aside{justify-content:flex-start;flex-grow:1}}

svg{width:200px;height:auto}

h1{margin:0 0 8px;font-weight:800;font-size:2rem;text-transform:uppercase;text-align:center;letter-spacing:0.03em}

h2{margin:0;font-weight:400;font-size:1rem;text-align:center}

form{margin-top:1rem;padding:40px;color:#222222;background-color:#ffffff}

noscript{color:#222222}

.form-title{display:block;margin-bottom:32px;font-size:32px;font-weight:700}

.form-email{display:block;width:100%;height:60px;padding:0 24px;border:2px solid #d1d5e0;border-radius:60px;font-size:18px;background-color:white}

.form-email:focus{outline:none;border:2px solid #f857a6}

.form-notices{margin:24px 0 32px}

.form-notices__title{font-size:18px;font-weight:700}

.form-checkbox{margin-top:16px;display:-webkit-box;display:-ms-flexbox;display:flex;align-items:center}

.form-checkbox__input{position:absolute;left:0;margin:0;width:0;height:0;opacity:0;overflow:hidden;pointer-events:none;z-index:-1}

.form-checkbox__text{position:relative;overflow:hidden;min-width:1em;min-height:1em;padding-left:1.25em;font-size:18px;cursor:pointer}

.form-checkbox__text:before{content:"";display:block;position:absolute;top:50%;left:0;width:1em;height:1em;box-sizing:border-box;border-radius:4px;border-width:2px;border-style:solid;border-color:#d1d5e0;background-color:#ffffff;transform:translateY(-50%);transition:border-color 50ms cubic-bezier(0.236, 0, 0.236, 1) 100ms,border-width 50ms cubic-bezier(0.236, 0, 0.236, 1) 50ms,background-color 50ms cubic-bezier(0.236, 0, 0.236, 1) 0s}

.form-checkbox__text:after{content:"";display:block;position:absolute;top:50%;left:0;width:1em;height:1em;opacity:0;text-align:center;color:#fff;background-image:url("data:image/svg+xml;base64,");background-size:0.7em;background-repeat:no-repeat;background-position:center;transform:translateY(-50%) scale(0);transition:transform 50ms cubic-bezier(0.236, 0, 0.236, 1) 50ms}

.form-checkbox__input:checked+.form-checkbox__text{color:#f857a6}

.form-checkbox__input:checked+.form-checkbox__text:before{border-width:0.5em;border-color:#f857a6;background-color:#f857a6;transition:border-width 50ms cubic-bezier(0.236, 0, 0.236, 1) 0s,background-color 50ms cubic-bezier(0.236, 0, 0.236, 1) 50ms}

.form-checkbox__input:checked+.form-checkbox__text:after{opacity:1;transform:translateY(-50%) scale(1)}

.form-submit{display:block;min-width:300px;padding:0 24px;border-radius:60px;font-size:24px;line-height:60px;font-weight:500;color:#ffffff;background-color:#f857a6;cursor:pointer;transition:box-shadow 150ms cubic-bezier(0.236, 0, 0.236, 1)}

.form-submit:hover{box-shadow:0 0 24px 0 #f857a6}

.form-submit:focus{outline:none;box-shadow:0 0 16px 4px rgba(0,0,0,0.3) inset}

.form-submit[disabled]{background-color:#d1d5e0;box-shadow:none}

@media only screen and (max-width: 600px){form{padding:16px}.form-title{font-size:24px;margin-bottom:24px}.form-email{height:48px;font-size:16px}.form-submit{line-height:48px;font-size:16px;width:100%}.form-notices__title{font-size:16px}.form-checkbox__text{font-size:16px}}

.link{text-decoration:none;font-weight:700}
